___CSS_LOADER_EXPORT___.push([module.id, `.login-container {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    margin: 3rem auto;
    margin-top: 6rem;
    padding: 1rem;
    max-width: 25rem;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    padding: 1.2rem;
    height: 350px;
    width: 430px;
  }
  
  .login-heading {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1.2rem;
    padding-top: 0.5rem;
    padding-left: 0.9rem;
    padding-bottom: 1rem;
  }
  
  .login-inputs {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 1.2rem;
  }
  
  .login-inputs input {
    padding: 0.6rem;
    margin-bottom: 0.6rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 65%;
    align-self: center;
  }
  
  .login-button {
    align-self: flex-end;
    padding: 0.6rem 1.2rem;
    font-size: 1rem;
    background-color: #21987a;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transform: translateX(-1.5rem);
  }
  
  .login-button:hover {
    background-color: #176a55;
  }
  
  .register-link {
    margin-top: 1.5rem;
    align-self: center;
  }
  
  .register-link a {
    margin-top: 1rem;
    text-decoration: none;
    font-size: 0.9rem;
    color: #21987a;
  }
  
  .register-link a:hover {
    text-decoration: underline;
  }`, "",{"version":3,"sources":["webpack://./static/css/login-page.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,oBAAoB;IACpB,iBAAiB;IACjB,gBAAgB;IAChB,aAAa;IACb,gBAAgB;IAChB,sBAAsB;IACtB,kBAAkB;IAClB,yBAAyB;IACzB,eAAe;IACf,aAAa;IACb,YAAY;EACd;;EAEA;IACE,eAAe;IACf,iBAAiB;IACjB,qBAAqB;IACrB,mBAAmB;IACnB,oBAAoB;IACpB,oBAAoB;EACtB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,qBAAqB;EACvB;;EAEA;IACE,eAAe;IACf,qBAAqB;IACrB,eAAe;IACf,sBAAsB;IACtB,kBAAkB;IAClB,UAAU;IACV,kBAAkB;EACpB;;EAEA;IACE,oBAAoB;IACpB,sBAAsB;IACtB,eAAe;IACf,yBAAyB;IACzB,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,8BAA8B;EAChC;;EAEA;IACE,yBAAyB;EAC3B;;EAEA;IACE,kBAAkB;IAClB,kBAAkB;EACpB;;EAEA;IACE,gBAAgB;IAChB,qBAAqB;IACrB,iBAAiB;IACjB,cAAc;EAChB;;EAEA;IACE,0BAA0B;EAC5B","sourcesContent":[".login-container {\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n    margin: 3rem auto;\n    margin-top: 6rem;\n    padding: 1rem;\n    max-width: 25rem;\n    border: 1px solid #ccc;\n    border-radius: 8px;\n    background-color: #f9f9f9;\n    padding: 1.2rem;\n    height: 350px;\n    width: 430px;\n  }\n  \n  .login-heading {\n    font-size: 2rem;\n    font-weight: bold;\n    margin-bottom: 1.2rem;\n    padding-top: 0.5rem;\n    padding-left: 0.9rem;\n    padding-bottom: 1rem;\n  }\n  \n  .login-inputs {\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    margin-bottom: 1.2rem;\n  }\n  \n  .login-inputs input {\n    padding: 0.6rem;\n    margin-bottom: 0.6rem;\n    font-size: 1rem;\n    border: 1px solid #ccc;\n    border-radius: 4px;\n    width: 65%;\n    align-self: center;\n  }\n  \n  .login-button {\n    align-self: flex-end;\n    padding: 0.6rem 1.2rem;\n    font-size: 1rem;\n    background-color: #21987a;\n    color: white;\n    border: none;\n    border-radius: 4px;\n    cursor: pointer;\n    transform: translateX(-1.5rem);\n  }\n  \n  .login-button:hover {\n    background-color: #176a55;\n  }\n  \n  .register-link {\n    margin-top: 1.5rem;\n